import React from 'react';

const EachList = ({details}) => 
{
  return (
    <tr>
      <td>{details.product}</td>
      <td>{details.qty}</td>
      <td>{details.price}</td>
      <td>{(+details.price * +details.qty).toFixed(2)}</td>
    </tr>
  );
};

export default EachList;



