import { LOAD_ITEM_LIST, ADD_ITEM_LIST } from '../Action/ActionTypes';
import InitialState from './initialState';

function ItemReducer(state = InitialState.items, action) {

  switch (action.type) {
      case LOAD_ITEM_LIST:
        return state;

      case ADD_ITEM_LIST:
        const len = state.list.length + 1;
        return {
          list: [ ...state.list, len],
          details: {
            ...state.details,
            [len]: action.item
          }
        };
        // return Object.assign({}, state, { details: {...state.details, action.item} });

      default:
        return state;
  }
}

export default ItemReducer;



