import React, { Component } from 'react';
import { Button, Form, FormGroup, Input } from 'reactstrap';

class AddItem extends Component {

  constructor(props) {
    super(props);
    this.add = this.add.bind(this);
    this.updateState = this.updateState.bind(this);    
    this.state = {
      productName: '',
      productQuantity: '',
      productPrice: '',
      error: ''
    }
  }

  updateState(event) {
    switch(event.target.dataset.id) {
      case 'productName':
        this.setState({
          productName: event.target.value
        });
        break;
      case 'productQuantity':
        this.setState({
          productQuantity: event.target.value
        });
        break;
      case 'productPrice':
        this.setState({
          productPrice: event.target.value
        });
        break;
      default:
        console.log('error');
    }
  }

  add() {
    if (
      this.state.productName != '' && 
      this.state.productQuantity != '' && 
      this.state.productPrice != ''
    ) {
      this.props.AddItemList({
        'product': this.state.productName,
        'qty': this.state.productQuantity,
        'price': this.state.productPrice
      });
      this.setState({
        productName: '',
        productQuantity: '',
        productPrice: '',
        error: ''
      });
    } else {
      this.setState({
        error: "Name, price and quantity is required"
      });
    }
  }

  render() {
    return (
      <div>
        {
          this.state.error ? <span>{this.state.error}</span> : null
        }
        <Form>
          <FormGroup>
            <Input type="text" name="productName" value={this.state.productName}
              placeholder="Product Name" data-id="productName" onChange={this.updateState} />
          </FormGroup>
          {' '}
          <FormGroup>
            <Input type="number" name="productQuantity" value={this.state.productQuantity}
              placeholder="Quantity" data-id="productQuantity" onChange={this.updateState}/>
          </FormGroup>
          {' '}
          <FormGroup>
            <Input type="number" name="productPrice" value={this.state.productPrice}
              placeholder="Price" data-id="productPrice" onChange={this.updateState}/>
          </FormGroup>
          <Button onClick={this.add}>Add Item</Button>
        </Form>
      </div>
    )
  }
};

export default AddItem;



